import interfaceurl from '@/utils/interface.js'
export default {
	data() {
		return {
			tabIndex: 0, //订单状态索引
			tabList: [{
				text: '全部',
				val: ''
			},{
				text: '待支付',
				val: '0'
			},{
				text: '待发货',
				val: '1'
			},{
				text: '已收货',
				val: '2'
			}],
			params: {
				page: 1, //页数
				//status: ''
			}, //分页参数
			orderPageData: {}, //订单数据
			orderPageList: [], //订单列表
			orderCount: [] //每个订单的商品个数
		}
	},
	onLoad(options) {
		
	},
	onShow() {
		this.initData()
	},
	//到达页面底部时触发的事件
	onReachBottom() {
		if (this.orderPageList.length >= this.orderPageData.total) {
			return;
		}
		this.params.page++;
		this.getOrderList()
	},
	methods: {
		initData() {
			//重置分页参数
			this.orderPageData = {}
			this.orderPageList = []
			this.params.page = 1
			this.getOrderList();
		},
		getOrderList() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.orderList, this.params, false).then((res) => {
				that.orderPageData = res.data
				if(that.params.page == 1) {
					that.orderPageList = res.data.data
				} else {
					that.orderPageList = that.orderPageList.concat(res.data.data)
				}
			});
		},
		selectTab(index, status) {
			this.tabIndex = index
			this.params.status = index == 0 ? '' : index
			this.initData()
		},
		setStatusText(status) {
			let text = ''
			if(status == 1) {
				text = '待支付'
			} else if(status == 2) {
				text = '待发货'
			} else if(status == 3) {
				text = '已收货'
			}
			return text
		}
	}
}
